        pro_id:project.pro_id,
        pro_name:project.pro_name,
        pro_desc:project.pro_desc,
        pro_docs:pro_docs,
      };
      //console.log(tranProject);
      //return;
      this.client.emit("scada/toServer/saveProject",tranProject,function(data){
