import  { getRandomString } from "@/common/util"

export default {

  // 
  pixel_default_script:`
  {
    /*@ 
    *  this.getAttr('attr_name');

    * key value 
        this.setState('rotate',1),rotate 

    * this.getState('')
    *
    */
    process:{

      /*
       
      */
      data_handler:function(){
      }
    },

    /*@ 
      * this.animate.rotate(animate_type),
        animate_type 120

      * this.setCtr(attr_name,value),
        attr_name attr
        value

      * this.getState('')

    */
    consume:{

      /*
       *  event mouse:over,mouse:down,
         mouse:up
      */
      Event:function(event){
      },

      /*
       *  (process) 
        this.setState()
      */
      consume_data:function(){
      },

    }
  }
  `,
  //   
  addPixelToDoc(canvas,url,doc_id){
    var _this = this;
    return new Promise(function(resolve,reject){
      fabric.Image.fromURL(url,function(obj){
        obj.scada_attr = {
          doc_id:doc_id,
          script:_this.pixel_default_script,
          guid:getRandomString(10),
          attr:[
            /* {attrName:'cesshi',tagName:'device1-1',"editor":"text"},
            {attrName:'cesshi1',tagName:'device1-2',"editor":"text"}, */
          ],
        }
        canvas.add(obj);
        resolve(obj);
      })
    })

  },

}



