<template>
    <div data-options="region:'north',border:false" style="height:28px;">

    <!--  dataDialog -->
    <div id="dataDialog" class="easyui-dialog" title="" data-options="modal:true,closed:true,iconCls:'icon-save'" style="width:330px;height:280px;padding:10px;">

        <div class="easyui-panel">
		    <form id="createNewProjectForm" method="post">
		        <table cellpadding="5">
		            <tr>
		                <td>:</td>
		                <td><input id="pro_name" class="easyui-textbox" type="text" name="name" data-options="required:true"></input></td>
		            </tr>
		            <tr>
		                <td>:</td>
		                <td><input class="easyui-textbox" type="text" name="describe" data-options="number:[0,9],validType:'length[1,10]'"></input></td>
		            </tr>
		            <tr>
		                <td>:</td>
		                <td><input class="easyui-numberbox" type="text" name="longitude" data-options="min:1000000,max:9999999999,precision:2"></input></td>
		            </tr>
		            <tr>
		                <td>:</td>
		                <td><input class="easyui-numberbox" type="text" name="latitude" data-options="min:0,precision:2"></input></td>
		            </tr>
		            <tr>
		                <td>:</td>
		                <td>
											<!-- <input class="easyui-textbox" type="text" name="management"></input> -->
											<input class="easyui-combobox" name="management" style="width:100%;" data-options="
			                    url:'/scada/getDeptList',
			                    method:'get',
			                    valueField:'DeptCode',
			                    textField:'Name',
			                    panelHeight:'auto',
			                    label: 'Language:',
			                    labelPosition: 'top'
			                    ">
										</td>
		            </tr>
		        </table>

		        <div style="text-align:center;padding:5px">
		            <a href="javascript:void(0)" class="easyui-linkbutton" @click="createNewProject"></a>
		            <a href="javascript:void(0)" class="easyui-linkbutton" onclick="clearAddProForm()"></a>
		        </div>
		    </form>
		</div>
    </div>
    <!--  end -->

    <!--  scriptDialog -->
    <div id="scriptDialog" class="easyui-dialog" title="" data-options="modal:true,closed:true,iconCls:'icon-save'" style="width:440px;height:670px;padding:10px;">
        <div class="easyui-panel">
          <Editor @contentUpdate="editorContentUpdate" ref="editorInstance"></Editor>
		    </div>
    </div>
    <!--  end -->

      <div>
        <a id="btn-home" href="#" class="easyui-menubutton" data-options="menu:'#mm0',plain:true"></a>
        <a href="#" class="easyui-menubutton" data-options="menu:'#mm1'"></a>
        <a href="#" class="easyui-menubutton" data-options="menu:'#mm2'"></a>
        <a href="#" class="easyui-menubutton" data-options="menu:'#mm3'"></a>
        <!-- <a href="#" class="easyui-menubutton" data-options="menu:'#mm4'"></a> -->
        <a href="#" class="easyui-menubutton" data-options="menu:'#mm5'"></a>
        <a href="javascript:void(0)" class="easyui-linkbutton" @click="openUserScript" data-options="plain:true"></a>
        <a href="#" class="easyui-menubutton" data-options="menu:'#mm6'"></a>
      </div>
      <div id="mm0" style="width:160px;">
        <div onclick=""></div>
        <div onclick="userSet();"></div>
        <div onclick="ctrlSet();"></div>
        <div onclick="extEleSet();"></div>
        <div onclick="logout();"></div>
      </div>
      <div id="mm1" style="width:150px;">
        <div data-options="iconCls:'icon-undo'" @click="createNewProject('showWin')"></div>
        <div onclick="getProList('open');"></div>
        <div onclick="getProList('edit');"></div>
        <div class="menu-sep"></div>
        <div data-options="iconCls:'icon-save'" @click="publish({topic:'saveProject',message:''})"></div>
      </div>
      <div id="mm2" style="width:160px;">
        <div onclick="opcDataSource();">OPC</div>
        <div onclick="openAddDataSource();">modbus</div>
          <div onclick="mgModbus();">ModBus</div>
        <div onclick="openbound();"></div>
        <div onclick="debugData();"></div>
      </div>
      <div id="mm3" style="width:150px;">
        <div data-options="iconCls:'icon-tip'" onclick="run();"></div>
      </div>

      <div id="mm5" style="width:160px;">
        <div onclick="hReport();"></div>
      </div>
      <div id="mm6" class="menu-content" style="padding:10px;text-align:left">
        <!-- <img src="easy/images/img.png" style="width:150px;height:50px"> -->
        <p style="font-size:14px;color:#444;">
          
        </p>
      </div>
    </div>
</template>

<script>
  import {mapGetters,mapMutations} from 'vuex';
  import Editor from '../editor/editor'
  import scada_model from '../scada/js/scada.model';
  export default {
    name: 'hello',
    props:['project'],
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    components:{
      Editor,
    },

    methods:{

      ...mapMutations({
        setCurrentProject:"project/setCurrentProject",
        setOpenDoc:'project/setOpenDoc',
        updatePixelScript:'project/updatePixelScript',
        publish:'MQ/publish',
      }),

      // 
      createNewProject(flag){
        if(this.project){
          $.messager.alert("",'','warning');
          return;
        }
        if(flag =="showWin"){
          $("#dataDialog").window("open");
          return;
        }
        else if(flag == "clear")
        {
          //$("#dataDialog").window("open");
          $("#createNewProjectForm").window("clear");
          return;
        }
        var pro_name = $("#pro_name").textbox("getValue");
        if(pro_name){

          var project = scada_model.createProject(pro_name);


          //console.log(project);
          //this.$emit("projectCreated",project);
          this.setCurrentProject(project); // state

          var mainDoc = scada_model.getProjectMainDoc(project);
          if(mainDoc)
          {
            this.setOpenDoc(mainDoc);
          }
          $("#dataDialog").window("close");
          //this.project = project;
        }
      },

      editorContentUpdate(value){
        //console.log(value);
        this.updatePixelScript(value);
        console.log("");
      },

      // 
      openUserScript(){
        if(!this.getActivePixel){
          return;
        }
        $("#scriptDialog").window("open");
        this.$refs.editorInstance.setValue(this.getActivePixel.scada_attr.script);

        //console.log(this.getActivePixel.scada_attr);
        //this.$refs.editorInstance.test();
      },


    },
    computed:{
      ...mapGetters({
          getActivePixel: "project/getActivePixel",
      }),
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



